.contact__wrapper {
  padding: 0 140px;
  display: flex;
  margin-bottom: 98px;
  margin-bottom: 2000px;
}

.contact__left-container {
  display: flex;
  flex-direction: column;
  width: calc(100%/2);
}

.contact__titile {
  font-family: 'Raleway', 'Arial', sans-serif;
  font-style: medium;
  font-weight: 400;
  font-size: 50px;
  line-height: 59px;
  color: #fff;
  margin-bottom: 86px;
}

.contact__text {
  width: 555px;
  font-family: 'Stemm', 'Arial', sans-serif;
  font-style: regular;
  font-weight: 400;
  font-size: 28px;
  line-height: 37px;
  color: #fff;
}

.contact__right-container {
  width: 864px;
  height: 866px;
}

.cotact__map {
  height: 864px;
  width: auto;
  overflow: hidden;
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
  filter: gray; /* IE 6-9 */
  transition: all 0.2s ease-in;
  margin-bottom: 35px;  
}

.cotact__map:hover {
  -webkit-filter: grayscale(0%);
  -moz-filter: grayscale(0%);
  -ms-filter: grayscale(0%);
  -o-filter: grayscale(0%);
  filter: grayscale(0%);
  filter: none; /* IE 6-9 */
  transition: all 0.2s ease-in;
}